import React, { useEffect, useState } from 'react';
import './App.css';
import stardust from './stardust.svg';
import 'vidstack/player/styles/default/theme.css';
import 'vidstack/player/styles/default/layouts/video.css';
import { useParams } from 'react-router-dom';
import 'vidstack/player';
import 'vidstack/player/layouts/default';
import 'vidstack/player/ui';
import {  } from 'react-adsense'

function Movie() {
  const { id } = useParams();
  const [movieData, setMovieData] = useState({});

  useEffect(() => {
    async function getMovieFromId(id) {
      try {
        const response = await fetch(`https://dashboard.mekobre.xyz/api/by-id-movie?id=${id}`);
        const data = await response.json();
        setMovieData(data[0]);
      } catch (error) {
        // Handle any errors
        console.log('Error:', error);
      }
    }

    getMovieFromId(id);
  }, [id]);

  return (
    <section id='movie'>
      <script async src="https://pagead2.googlesyndication.com/pagead/js/adsbygoogle.js?client=ca-pub-8090972778897193"
            crossorigin="anonymous"></script>
        <ins class="adsbygoogle"
            style={{display: 'block'}}
            data-ad-format="autorelaxed"
            data-ad-client="ca-pub-8090972778897193"
            data-ad-slot="9089167709"></ins>
        <script>
            (adsbygoogle = window.adsbygoogle || []).push({});
        </script>
      <div id='movie-display'>
        <amp-ad width="100vw" height="320"
            type="adsense"
            data-ad-client="ca-pub-8090972778897193"
            data-ad-slot="1205974106"
            data-auto-format="rspv"
            data-full-width="">
          <div overflow=""></div>
        </amp-ad>
        <media-player title="Sprite Fight" src={movieData.url}>
          <media-provider></media-provider>
          <media-video-layout thumbnails={`${movieData.poster_link}`}></media-video-layout>
        </media-player>
      </div>

      <div id='movie-info'>
        <div id='cover-for-movie'>
          <img src={movieData.poster_link}></img>
          <span></span>
          <div id='textInfo'>
            <h1>{movieData.qartuli_saxeli}</h1>
            <h3>{movieData.inglisuri_saxeli}</h3>
            <span></span>
            <h5>{`${String(movieData.description).slice(0, 165)}${(String(movieData.description).length > 200) ? "..." : ""}`}</h5>
          </div>
        </div>
      </div>

    </section>
  );
}

export default Movie;