import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import IMDB from './imdb.svg'

const Main = () => {
  const [jsonData, setJsonData] = useState([]);
  
  async function getMovieById(id) {
    try {
      const response = await axios.get(`https://dashboard.mekobre.xyz/api/by-id-movie?id=${id}`);
      return response.data;
    } catch (error) {
      console.log('Error fetching data:', error);
      throw error;
    }
  }

  const handleButtonClick = async (id) => {
    try {
      const response = await fetch(`https://dashboard.mekobre.xyz/api/by-id-movie?id=${id}`);
      const data = await response.json();
      console.log(data[0]);
      return data[0];
    } catch (error) {
      // Handle any errors
      console.log('Error:', error);
    }
  };

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get('https://dashboard.mekobre.xyz/api/movie-carousel');
        setJsonData(response.data);
      } catch (error) {
        console.log('Error fetching data:', error);
      }
    };

    fetchData();
  }, []);

  const [slide, setSlide] = useState(Math.floor(Math.random() * 5));
  var actualSlide = slide
    useEffect(() => {
        //Implementing the setInterval method
        const interval = setInterval(() => {
            setSlide((actualSlide + 1) % 7);
            actualSlide = (actualSlide + 1) % 7
        }, 3000);
 
        //Clearing the interval
        return () => clearInterval(interval);
    }, []);

  return (
    <main>
      <section id="movie-slider">
        <div id='image-list'>
          {jsonData.map((data, index) => (
            <div key={index} id={`page${index}`} className="image-display" style={{ opacity: index === slide ? 1 : 0 }}>
              <div className="slider-image" style={{ backgroundImage: `url(${data.movie_carousel_banner_url})` }}></div>
              <div className="top-gradient"></div>
              <div className="image-info">
                <h3 className="desc-slider">{`${data.movie_carousel_agwera.slice(0, 200)}${(data.movie_carousel_agwera.length > 200) ? "..." : ""}`}</h3>
                <h1 className="title-slider" >{`${String(data.movie_carousel_title).slice(0,20)}${(data.movie_carousel_title.length > 20) ? "..." : ""}`}</h1>
                <div className='imdb-rating'><img src={IMDB} /> <h2>{data.movie_carousel_imdb}</h2></div>
                <Link to={`movie/${data.id}`}>
                  <button className='play-btn'>
                    <div className='cover'></div>
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 384 512"><path d="M73 39c-14.8-9.1-33.4-9.4-48.5-.9S0 62.6 0 80V432c0 17.4 9.4 33.4 24.5 41.9s33.7 8.1 48.5-.9L361 297c14.3-8.7 23-24.2 23-41s-8.7-32.2-23-41L73 39z"/></svg>
                  </button>
                </Link>
              </div>
            </div>
          ))}
        </div>
        <div id="progression-slider">
          <div id='slider-cover'></div>
          {[0,1,2,3,4,5,6].map((_, index) => (
            <button key={index} className="move-slider" id={`move-${index}`} style={{backgroundImage: index === slide ? "linear-gradient(90deg, #3498db, #9a52ff, #8e44ad, #9a52ff)" : "none" }} onClick={() => {
              var PrevSlide = document.getElementById(`page${slide}`)
              PrevSlide.style.opacity = "0"
              var PrevBtn = document.getElementById(`move-${slide}`)
              PrevBtn.style.backgroundImage = "none"
              setSlide((index) % 5)
              console.log(actualSlide, 'press')
              document.getElementById(`page${slide}`).style.opacity = "1"
              document.getElementById(`move-${slide}`).style.backgroundImage = "linear-gradient(90deg, #3498db, #9a52ff, #8e44ad, #9a52ff)"
            }}></button>
          ))}
        </div>
      </section>
    </main>
  );
};

export default Main;